body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.c-card-blog {
  width: 18.5rem;
  margin: 0 auto;
  filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.25));
}

.c-card-blog__body {
  padding: 1.25rem;
  background-color: #D2D2D2;
  border-radius: 0px 0px 10px 10px;
}

.c-card-blog__title {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  font-size: 1.25rem;
  color: #000;
}

.c-card-blog__text {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  color: #000;
  margin-top: 0.625rem;
}